@import "../variables.scss";

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.contact {
  background-color: $transparentBgColor;
  padding: 30px 70px;
  border-radius: $border-radius;
}

.row {
  width: 100%;
  height: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 0;
  justify-content: flex-start;

  color: $textColor;
  font-size: 20px !important;
  fill: $textColor;
}

.row:hover {
  color: $bandColor;
  fill: $bandColor;
}

.icon {
  height: 100%;
  width: 40px;
  margin-right: 10px;
}

a:visited {
  color: inherit;
}

