@import "../variables.scss";

.container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.bioWrapper {
  max-width: 70%;
  background-color: $transparentBgColor;
  padding: $padding;
  border-radius: $border-radius;
  font-size: 22px;
}

.bandName {
  color: $bandColor;
}

@media (min-width: $min-width) {
  .bioWrapper {
    max-width: 40%;
  }
}
