@import "../variables.scss";

.container {
  width: 100%;
  color: $textColor;

  padding-top: 50px;
}

.videoContainer {
  padding: 0;
}

.pressContainer {
  text-align: center;
  padding: 0 5%;
}

.pressHeader {
  font-size: 22px;
  padding: 10px 0;
}

.pressQuote {
  font-style: italic;
  padding: 5px 0;
}

@media (min-width: $min-width) {
  .videoContainer {
    padding: 0 25%;
  }
}
