@import "../variables.scss";

.pageContainer {
  width: 100%;
  height: 100%;
  background-image: url("../../assets/images/background-with-logo.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  font-family: "mic-32";
  color: $textColor;
}

.scrollableContent {
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.headerContainer {
  margin: 50px 0;
  display: flex;
  justify-content: center;
  width: 100%;
  position: sticky;
  top: 0;
}

.header {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.content {
  z-index: 2;
  flex: 1;
}

$navLinkColor: white;

.navLink {
  color: $navLinkColor;
  text-decoration: none;
  font-size: 40px;
}

.navLink:visited {
  color: $navLinkColor;
}

.navLinkActive {
  color: green !important;
}
