@import "../variables.scss";

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tours {
  padding: $padding;
  background-color: green;
  border-radius: $border-radius;
}

.header {
  padding: 10px 0;
  font-size: 28px;
  position: sticky;
  top: 50px;
  z-index: 5;
  background-color: green;
}

.tourEntry {
  padding: 20px 0;
}
